.featured {
  height: 100vh;
}

.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}

.featured--name {
  font-size: 60px;
  font-weight: bold;
}

.info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.points, .year, .seasons {
  display: inline-block;
  margin-right: 15px;
}

.points {
  color: #46d369;
}

.description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.btns {
  margin-top: 15px;
}

.whatbtn, .blackbtn {
  display: inline-block;
  font-size: 20px;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity: 1;
  transition: all ease 0.2s;
}

.whatbtn {
  background-color: #ffff;
  color: #000;
}

.blackbtn {
  background-color: #333;
  color: #ffff;
}

.whatbtn:hover, .blackbtn:hover {
  opacity: 0.7;
}

.genres {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media (max-width:768px) {
  .featured {
    height: 98vh;
  }
  .featured--name {
    font-size: 40px;
  }
  .info {
    font-size: 16px;
  }
  .description {
    font-size: 20px;
    max-width: 100%;
    margin-right: 30px;
  }
  .blackbtn, .whatbtn {
    font-size: 16px;
  }
  .genres {
    font-size: 14px;
  }
}