@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #fff;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lists {
  margin-top: -150px;
}

footer {
  margin: 50px 0;
  text-align: center;
}

.loading img {
  width: 100%;
  height: 100%;
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  z-index: 99;
}
.movieRow {
  margin-bottom: 30px;
}

.movieRow h2 {
  margin: 0px 0px 0px 30px;
}

.movieRow--listarea {
  overflow-x: hidden;
  padding-left: 30px;
}

.movieRow--list {
  transition: all ease 0.5s;
}

.movieRow--item {
  display: inline-block;
  width: 150px;
}

.movieRow--item img {
  width: 100%;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: all ease 0.2s;
  cursor: pointer;
}

.movieRow--item img:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.arrow-left, .arrow-right {
  font-size: 35;
  color: red;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 40px;
  height: 205px;
  z-index: 99;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: hidden;
  margin-top: 10px;
  cursor: pointer;
  transition: all ease 0.5s;
  opacity: 0;
}

.arrow-left {
  left: 0;
}

.arrow-right {
  right: 0;
}

.movieRow:hover .arrow-left, .movieRow:hover .arrow-right {
  opacity: 1;
}

@media (max-width:800px) {
  .arrow-left, .arrow-right {
    opacity: 1;
  }
}
.featured {
  height: 100vh;
}

.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}

.featured--name {
  font-size: 60px;
  font-weight: bold;
}

.info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.points, .year, .seasons {
  display: inline-block;
  margin-right: 15px;
}

.points {
  color: #46d369;
}

.description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.btns {
  margin-top: 15px;
}

.whatbtn, .blackbtn {
  display: inline-block;
  font-size: 20px;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity: 1;
  transition: all ease 0.2s;
}

.whatbtn {
  background-color: #ffff;
  color: #000;
}

.blackbtn {
  background-color: #333;
  color: #ffff;
}

.whatbtn:hover, .blackbtn:hover {
  opacity: 0.7;
}

.genres {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media (max-width:768px) {
  .featured {
    height: 98vh;
  }
  .featured--name {
    font-size: 40px;
  }
  .info {
    font-size: 16px;
  }
  .description {
    font-size: 20px;
    max-width: 100%;
    margin-right: 30px;
  }
  .blackbtn, .whatbtn {
    font-size: 16px;
  }
  .genres {
    font-size: 14px;
  }
}
header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 30px;
  background: transparent;
  transition: all ease 0.5s;
}

.black {
  background-color: #141414;
}

.logo {
  height: 80px;
}

.logo img {
  height: 100%;
}

.user {
  height: 35px;
}

.user img {
  height: 100%;
  border-radius: 3px;
}
